<template>
  <div class="financial-station">
    <Header text="我的营收">
      <a
        href="javascript:;"
        @click="jumpToBill({fundType: '4', fundState:''})"
        slot="right"
        v-if="showFee"
      >手续费</a>
    </Header>
    <div
      class="warp-box"
      v-if="show"
    >
      <div class="tab-list">
        <div
          class="tab-item"
          v-for="(item, index) in permissionList"
          @click="changeTab(index)"
          :key="index"
        >
          <div
            class="icon"
            :style="{background: `url(${$cdnImageUrl}/mine/sales/${item.icon}) center center / cover no-repeat`}"
          ></div>
          <div
            class="title"
            :class="activeIndex === index ? 'active' : ''"
          >{{item.title}}</div>
        </div>
      </div>
      <div class="content-warp">
        <template v-for="(item, index) in detailList">
          <div
            class="financial-detail"
            v-if="item.hide === false"
            :key="index"
          >
            <div class="title"><span></span><span>{{item.title}}</span></div>
            <div class="num"><span>￥</span><span>{{item.num}}</span></div>
            <div
              v-if="!(permissionList[activeIndex].type === 6 || (permissionList[activeIndex].type === 5 && index !== 0))"
              class="check-bill"
              @click="jumpToBill({fundState:item.type})"
            >查看账单</div>
          </div>
        </template>
      </div>
      <div class="footer">
        <div
          class="unbind-card"
          v-if="bindCard"
          @click="jumpToUnbindCard"
        >解绑银行卡</div>
        <div
          class="bind-card"
          v-else
        >您还没有绑定银行卡 ，点击这里<i @click="jumpToBindCard">去绑卡>></i></div>
        <div
          class="rules"
          @click="jumpToRules"
        ><i></i><span>规则说明
          </span></div>
      </div>
    </div>

    <van-loading
      type="spinner"
      color="#1989fa"
      class="cover-loading"
      v-if="showLoading"
    />
  </div>
</template>

<script>
import Header from '@/components/header'
import { checkBindByType, getComminsionSumInfo, myManageinfo, getServiceCharge } from '@/http/service/mine'

const permissionMap = {
  2: {
    title: '佣金',
    type: 2,
    billType: 2,
    icon: 'yongjin.png',
  },
  1: {
    title: '货款',
    type: 1,
    billType: 1,
    icon: 'huokuan.png',
  },
  5: {
    title: '奖励',
    type: 5,
    billType: 5,
    icon: 'jiangli.png',
  },
  6: {
    title: '服务费', // 后端访问服务费是新接口:后续扩展type从6起
    type: 6,
    billType: 6,
    icon: 'service-icon.png',
  },
  7: {
    title: '设计师奖励',
    type: 7,
    billType: 13,
    icon: 'reward.png',
  }
}

export default {
  components: { Header },
  data() {
    return {
      show: false,
      identifyFlag: '', // 用户身份
      showLoading: true,
      showFee: false,
      bindCard: false, // 是否绑卡
      activeIndex: 0,
      permissionList: [],
      detailList: [
        {
          title: '预计收益',
          num: '--',
          type: 1,
          hide: false,
        },
        {
          title: '提现中',
          num: '--',
          type: 2,
          hide: false,
        },
        {
          title: '已提现',
          num: '--',
          type: 3,
          hide: false,
        },
      ],
    }
  },
  created() {
    this.showLoading = true
    Promise.all([
      checkBindByType({ type: 'bank' }),
      getComminsionSumInfo({
        accountType: 2, // 默认请求佣金收益
      }),
      myManageinfo(),
    ]).then(res => {
      this.showLoading = false
      const data = res.map(v => v.data.success ? v.data.data : false)
      if (data[2]) {
        this.identifyFlag = Number(data[2].identifyFlag)
        // const permissionKeys = this.permissionFliter(99100)
        const permissionKeys = this.permissionFliter(Number(data[2].occupation))
        if (!permissionKeys.length) {
          this.$router.replace('/financialStationLocked')
          return
        }
        this.permissionList = permissionKeys.map(type => permissionMap[type])
      }

      this.bindCard = data[0]

      this.formData(data[1])

      this.show = true
    })
  },
  methods: {
    permissionFliter(occupation) {
      // identifyFlag: 0普通用户 10-20店主 21顾问 22普通合伙人 31 平台合伙人
      // occupation=99100 设计师(合伙人的一种)
      let res = []
      if (occupation === 99100) {
        res = [2, 5]
        if (this.$util.isSyBird()) {
          res.push(6)
        }
      } else if ([21, 22, 31].includes(this.identifyFlag)) {
        res = [2, 5]
      } else if (this.identifyFlag >= 10 && this.identifyFlag <= 20) {
        this.showFee = true
        res = [2, 1, 5]
      }
      res.push(7)
      return res
    },
    formData(data) {
      if (!data) return
      this.detailList[0].num = Number(data.balance).toFixed(2)
      this.detailList[1].num = Number(data.withdrawingBalance).toFixed(2)
      this.detailList[2].num = Number(data.withdrawedBalance).toFixed(2)
    },
    fetchSumInfo() {
      this.showLoading = true
      getComminsionSumInfo({
        accountType: this.permissionList[this.activeIndex].type,
      }).then(res => {
        this.showLoading = false
        let result = res && res.data
        if (result.success) {
          this.formData(result.data)
        }
      })
    },
    fetchServiceCharge() {
      this.showLoading = true
      getServiceCharge().then(res => {
        this.showLoading = false
        let result = res && res.data
        if (result.success) {
          this.detailList[0].num = Number(result.data.expectedEarning).toFixed(2)
          this.detailList[1].num = 0.0
          this.detailList[2].num = Number(result.data.withdrawal).toFixed(2)
        } else {
          this.detailList[0].num = '--'
          this.detailList[1].num = '--'
          this.detailList[2].num = '--'
        }
      })
    },
    changeTab(index) {
      this.activeIndex = index
      if (this.permissionList[this.activeIndex].type === 6) {
        // 服务费不展示提现中
        this.detailList[1].hide = true
        this.fetchServiceCharge()
      } else {
        this.detailList[1].hide = false
        this.fetchSumInfo()
      }
    },
    jumpToBindCard() {
      this.$router.push({ path: '/bindcard', query: { identifyFlag: this.identifyFlag } })
    },
    jumpToUnbindCard() {
      this.$router.push({ path: '/bankcard' })
    },
    jumpToRules() {
      this.$router.push({ path: 'documentInfo', query: { id: 15, title: '规则说明' } })
    },
    jumpToBill(params) {
      if (this.permissionList && this.permissionList[this.activeIndex] && this.permissionList[this.activeIndex].type === 1) {
        // DSG-27887 货款查看账单埋点
        this.$util.gioTrackWithVariable('mall_income_click_bill')
      }
      this.$router.push({
        path: '/billList',
        query: {
          fundType: params.fundType || this.permissionList[this.activeIndex].billType,
          fundState: params.fundState,
        },
      })
    },
  },
}
</script>

